<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Pesquisar Rede">
          <v-card class="elevation-0">
            <v-card-text>
              <v-form>
                <v-layout
                  row
                  wrap>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.nome"
                      label="Nome Rede"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.sigla"
                      label="Sigla Rede"
                    />
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                :loading="loadingBtn"
                color="primary"
                @click="pesquisar"
              >Pesquisar</v-btn>
            </v-card-actions>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card>
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Redes</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical
            />
            <v-spacer />
            <v-btn
              small
              fab
              color="primary">
              <v-icon @click="newItem()">mdi-plus</v-icon>
            </v-btn>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            :total-items="totalItems"
            :pagination.sync="pagination"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="headerCell"
              slot-scope="{ header }">
              <span
                class="subheading font-weight-light text--darken-3"
                v-text="header.text" />
            </template>
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5
            >
              <td>{{ item.nome }}</td>
              <td>{{ item.sigla }}</td>
              <td class="justify-end layout ma-2">
                <v-icon
                  class="mr-2"
                  color="blue"
                  title="Editar dados da rede"
                  @click="editItem(item)"
                >mdi-pencil</v-icon>
                <v-icon
                  color="red"
                  title="Excluir rede"
                  @click="openDialogDelete(item)"
                >mdi-delete</v-icon>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <template
        v-if="dialog"
      >
        <v-layout
          row
          justify-center>
          <v-dialog
            v-model="dialog"
            max-width="390">
            <v-card>
              <v-card-title class="headline">Excluir Rede</v-card-title>

              <v-card-text>
                Confirma a exclusão da Rede <span class="title">{{ item.nome }}</span>? Essa operação não poderá ser desfeita.
              </v-card-text>

              <v-card-actions>
                <v-spacer />

                <v-btn
                  outline
                  style="color: black !important"
                  @click="closeDialogDelete">Cancelar</v-btn>

                <v-btn
                  color="red"
                  @click="deleteItem">Excluir</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </template>
    </v-layout>
  </v-container>
</template>

<script>
import RedeBusiness from '../../business/RedeBusiness'

export default {
  metaInfo: {
    titleTemplate: '%s - Redes'
  },
  data () {
    return {
      dialog: false,
      item: null,
      loading: false,
      loadingBtn: false,
      totalItems: 0,
      pagination: {
        pageNo: 1,
        rowsPerPage: 25,
        sortBy: 'nome',
        descending: true
      },
      selected: [],
      filtros: {
        nome: '',
        sigla: ''
      },
      search: '',
      headers: [
        {
          text: 'Nome Rede',
          value: 'nome',
          sortable: false
        },
        {
          text: 'Sigla Rede',
          value: 'sigla',
          sortable: false
        },
        {
          sortable: false,
          text: ''
        }
      ],
      items: []
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  methods: {
    reload () {
      this.paginar()
    },
    newItem () {
      this.$router.push('cad-rede')
    },
    editItem (item) {
      this.$router.push(`cad-rede/${item.id}`)
      this.reload()
    },
    openDialogDelete (item) {
      this.item = item
      this.dialog = true
    },
    closeDialogDelete () {
      this.item = null
      this.dialog = false
    },
    deleteItem () {
      RedeBusiness.delete(this.item.id)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.closeDialogDelete()
          this.reload()
        })
        .catch(error => this.$root.showErro(error.data))
    },
    pesquisar () {
      this.loadingBtn = true
      this.paginar()
    },
    paginar () {
      this.loading = true
      RedeBusiness.pagination(this.pagination, this.filtros).then(response => {
        this.items = response.data.content
        this.totalItems = response.data.totalElements
      }).finally(() => {
        this.loadingBtn = false
        this.loading = false
      })
    }
  }
}
</script>
